import './vendor';
import mmenu from 'mmenu-light';
import Swiper, { Navigation, Pagination, EffectFade, Autoplay } from 'swiper';
import { Fancybox, Carousel, Panzoom } from "@fancyapps/ui";

import './helpers';
import './components/social';
import { ieFix } from './vendor/ie-fix';
import { vhFix } from './vendor/vh-fix';
import { actualYear } from './modules/actualYear';
import lazyLoading from './modules/lazyLoading';
import scrollToAnchor from './modules/scrollToAnchor';

ieFix();
vhFix();
actualYear();
scrollToAnchor.init();

lazyLoading.init();



const menu = new mmenu(
    document.querySelector("#mmenu"),
    // "(max-width: 600px)"
);


const navigator = menu.navigation(
    {
        theme: "dark"
    }
);


const drawer = menu.offcanvas({
    position: "right"
});

document.addEventListener(
    "DOMContentLoaded", () => {
        const burgerBtn = document.getElementById("mmenu-burdger")

        burgerBtn.addEventListener("click", (event) => {
            event.stopPropagation();
            event.preventDefault();
            burgerBtn.classList.toggle('is-active');
            if (burgerBtn.classList.contains('is-active')) {
                drawer.open();
            } else {
                drawer.close();
            }
        });

        var swiper1 = new Swiper(".slider-hero", {
            modules: [Navigation, Pagination, EffectFade, Autoplay],
            preloadImages: true,
            lazy: true,
            autoHeight: false,
            fadeEffect: {
                crossFade: true
            },
            loop: true,
            speed: 300,
            autoplay: {
                delay: 3000,
                disableOnInteraction: false,
            },
            pagination: {
                el: ".slider-hero .swiper-pagination",
                clickable: true,
            },
            navigation: {
                nextEl: '.slider-hero .swiper-button-next',
                prevEl: '.slider-hero .swiper-button-prev',
            },
        });

        Fancybox.bind("[data-fancybox-plyr]");


        var swiper2 = new Swiper(".slider-kids", {
            slidesPerView: 3,
            centeredSlides: false,
            spaceBetween: 70,
            grabCursor: true,
        });

        var swiper3 = new Swiper(".wehave__carousel", {
            slidesPerView: 3,
            centeredSlides: false,
            spaceBetween: 15,
            grabCursor: true,
            loop: true,
        });

        
        var swiper4 = new Swiper(".reviews__carousel", {
            slidesPerView: 3,
            centeredSlides: false,
            spaceBetween: 0,
            grabCursor: true,
        });

        
        var swiper5 = new Swiper(".gallery__carousel", {
            slidesPerView: 3,
            centeredSlides: false,
            spaceBetween: 15,
            grabCursor: true,
            loop: true,
            initialSlide: 3,
        });

    }
);
